// papers
import Paper from '../pages/papers/paper';
import My_Papers from '../pages/papers/my_papers';
import TiO2MoS2 from '../pages/papers/TiO2MoS2';

//AI
import Intro from '../pages/AI/intro';
import AI from '../pages/AI/ai';
import ML from '../pages/AI/ml';
import DL_1 from '../pages/AI/dl_1';
import DL_2 from '../pages/AI/dl_2';
import DL_3 from '../pages/AI/dl_3';
