import Coating from '../pages/applications/coating';

import Energy from '../pages/applications/energy';
import Co2 from '../pages/applications/co2';

import TMoN from '../pages/applications/TMoN';

// test
// import Test from '../pages/test';
// import Git from '../pages/git';

// Route path='/page1' === Nav.Link href="/hawon/" 
//   실제 로드되는 페이지        어드레스에 표시

function NavRoute() {
  return (
    <>
      <Switch>
        <Route path={'/'} exact component={Home} />

        {/* // references */}
        <Route path={'/catalyst/catalyst1'} component={Catalyst1} />
        <Route path={'/catalyst/photoreactor'} component={Photoreactor} />
        <Route path={'/catalyst/provskites'} component={Provskites} />
        <Route path={'/catalyst/anodizing'} component={Anodizing} />
        <Route path={'/catalyst/n_reduction'} component={N_reduction} />
        <Route path={'/catalyst/catalyst_old'} component={Catalyst_old} />
        <Route path={'/catalyst/Catalyst_old2'} component={Catalyst_old2} />
        <Route path={'/catalyst/lorentz_force'} component={Lorentz_force} />
        <Route path={'/catalyst/quantumdots'} component={Quantumdots} />
        <Route path={'/catalyst/black_tio2'} component={Black_tio2} />
        <Route path={'/catalyst/tmdcepitaxy'} component={TMDCEpitaxy} />
        <Route path={'/catalyst/tio2mos2'} component={Tio2mos2} />
        <Route path={'/catalyst/Photothermal'} component={Photothermal} />
        <Route path={'/catalyst/up_conversion'} component={up_conversion} />
        <Route path={'/catalyst/Photon_avalanche'} component={Photon_avalanche} />
        <Route path={'/catalyst/plasmon'} component={plasmon} />
        <Route path={'/catalyst/two_dimensional'} component={two_dimensional} />    

        {/* // theory */}
        <Route path={'/theory/BandTheory'} component={BandTheory} />
        <Route path={'/theory/ElectronicBandTheory'} component={ElectronicBandTheory} />
        <Route path={'/theory/제일원리'} exact component={제일원리} />
        <Route path={'/theory/상태밀도함수'} exact component={상태밀도함수} />
        <Route path={'/theory/d_Band_theory'} component={D_Band_theory} />
        <Route path={'/theory/양자점'} exact component={양자점} />
        <Route path={'/theory/Z_scheme'} exact component={Z_scheme} />
        <Route path={'/theory/H_junction'} exact component={H_junction} />
        <Route path={'/theory/촉매활성'} exact component={촉매활성} />
        <Route path={'/theory/서울대'} component={서울대} />

        {/* // analysis */}
        <Route path={'/analysis/표면분석'} exact component={표면분석} />
        <Route path={'/analysis/XRD'} component={XRD} />
        <Route path={'/analysis/Morphology'} component={Morphology} />
        <Route path={'/analysis/XPS'} component={XPS} />
        <Route path={'/analysis/Optical'} component={Optical} />
        <Route path={'/analysis/Raman'} component={Raman} />
        <Route path={'/analysis/EDOS'} exact component={EDOS} />
        <Route path={'/analysis/PDOS'} exact component={PDOS} />
        <Route path={'/analysis/Surface'} component={Surface} />
        <Route path={'/analysis/계산화학'} component={계산화학} />

        {/* // applications */}
        <Route path={'/applications/TiO2_Nano'} component={TiO2_Nano} />
        <Route path={'/applications/MoS2_1T'} component={MoS2_1T} />
        <Route path={'/applications/Anodizing_mk'} component={Anodizing_mk} />
        <Route path={'/applications/Black_TiO2_tm'} component={Black_TiO2_tm} />
        <Route path={'/applications/Molybdenum_Nitride'} component={Molybdenum_Nitride} />

        <Route path={'/applications/MoS2CaTiO3_sm'} component={MoS2CaTiO3_sm} />
        <Route path={'/applications/MoS2TiO2_sm'} component={MoS2TiO2_sm} />
        <Route path={'/applications/MoS2_1T_tm'} component={MoS2_1T_tm} />
        <Route path={'/applications/MoS2_iT_POF'} component={MoS2_iT_POF} />
        
        <Route path={'/applications/MoN2TiO2_sm'} component={MoN2TiO2_sm} />
        <Route path={'/applications/MoN2_1T_tm'} component={MoN2_1T_tm} />
        <Route path={'/applications/MoN2_iT_POF'} component={MoN2_iT_POF} />
        <Route path={'/applications/MoN2'} component={MoN2} />
        
        {/* <Route path={'/applications/catalyst_make'} component={catalyst_make} /> */}
        <Route path={'/applications/PMMA'} component={PMMA} />
        <Route path={'/applications/coating'} component={Coating} />
        <Route path={'/applications/Energy'} exact component={Energy} />
        <Route path={'/applications/Co2'} component={Co2} />
        <Route path={'/applications/TMoN'} component={TMoN} />  

        {/* // kinetic */}
        <Route path={'/kinetics/ref'} exact component={Ref} />
        <Route path={'/kinetics/usersmenual'} component={UsersMenual} />
        <Route path={'/kinetics/control'} component={ControlDescription} />
        <Route path={'/kinetics/model'} component={ModelDescription} />
        <Route path={'/kinetics/species'} component={SpeciesDescription} />
        <Route path={'/kinetics/param'} component={ParamDescription} />
        <Route path={'/kinetics/therm'} component={ThermDescription} />
        <Route path={'/kinetics/fitdata'} component={FitdataDescription} />
        <Route path={'/kinetics/kinetics'} component={KinetcsAnalysis} />

        {/* // papers */}
        <Route path={'/papers/Paper'} exact component={Paper} />
        <Route path={'/papers/my_papers'} component={My_Papers} />
        <Route path={'/papers/TiO2MoS2'} component={TiO2MoS2} />    

        {/* AI */}
        <Route path={'/AI/intro'} component={Intro} />
        <Route path={'/AI/ai'} component={AI} />
        <Route path={'/AI/ml'} component={ML} />
        <Route path={'/AI/dl_1'} component={DL_1} />
        <Route path={'/AI/dl_2'} component={DL_2} />
        <Route path={'/AI/dl_3'} component={DL_3} />
        <Route path={'/AI/ai_ref'} component={AI_Ref} />

        {/* Program */}
        <Route path={'/Programs/MLP/p1_Basics_of_MLP'} component={p1_Basics_of_MLP} />
        <Route path={'/Programs/MLP/p1_1_regression'} component={p1_1_regression} />


        {/* NeuroComputing */}
        <Route path={'/neurocomputing/ch1/ch1_1'} component={ch1_1} />
        <Route path={'/neurocomputing/ch1/ch1_2'} component={ch1_2} />
        <Route path={'/neurocomputing/ch1/ch1_3'} component={ch1_3} />
        <Route path={'/neurocomputing/ch2/ch2_1'} component={ch2_1} />
        <Route path={'/neurocomputing/ch2/ch2_2'} component={ch2_2} />
        <Route path={'/neurocomputing/ch2/ch2_3'} component={ch2_3} />

        {/* // Test */}
        {/* <Route path={'/test'} component={Test} /> */}
        {/* <Route path={'/git'} component={Git} /> */}


      </Switch>
      
      {/* console.log('public url: ', {process.env.PUBLIC_URL}) */}
    </>

  )
};
export default NavRoute;